<template >
<div>
    <el-table :data="tableData"
    stripe
    style="width: 100%">

    <el-table-column
      label=""
      width="100">
    </el-table-column>

    <el-table-column type="expand">
      <template slot-scope="props">
        <div style="text-align:center">
        <el-form label-position="left" inline class="demo-table-expand">
            <span >{{ props.row.detail }}</span>
        </el-form>
         <el-image
          style="width: 250px; height: 250px;margin:30px"
          :src="props.row.image"
          :fit="fit"></el-image>
        </div>
      </template>
    </el-table-column>
      
    <el-table-column
      label="图片"
      align = "center"
      width="280">
      <template scope="scope">
      <el-image
      style="width: 50px; height: 50px"
      :src="scope.row.image"
      :fit="fit"></el-image>
      </template>
    </el-table-column>

     <el-table-column
      prop="title"
      label="标题"
      align="center"
      width="280">
     </el-table-column>

       <el-table-column
      prop="category"
      label="性别"
      align="center"
      width="280">
       </el-table-column>

      <el-table-column
      prop="poston"
      align="center"
      label="发布时间">
     </el-table-column>

       <el-table-column
      fixed="right"
      label=""
      width="100">
      <template slot-scope="scope">
        <el-button @click="focusHandle(scope.row)" type="text" size="small" >关注</el-button>
      </template>
    </el-table-column>
    
    </el-table>
    <div style="text-align:center;margin-top:30px">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left">上一页</el-button>
        <el-button type="primary">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
</div>
</template>


<script>
  export default {
    data() {
      return {
        tableData: [],
        page: {
          pageIndex: '1',
          pageSize:  '10'
        },
      
        dialogVisible: false,
        imageUrl: '',
        sex: '1',
        fit: '',
        tips: ''
      
      }
    },
     created() {
        this.getDataList();
    },
    activated() {
      this.getDataList();
    },
    methods: {
     
      async getDataList() {
        const data = await this.$api.missingPersonList(this.page);
        this.tableData = data;
      },
      async focusHandle(row){
        const data = await this.$api.focus(row.id);
        if(!data){
          return false;
        }
        this.$message({
          message: '已添加到关注列表',
          type: 'success',
          duration: 1000
        });
        this.getDataList()
        //row.disable = ture
      }
    }
  }
</script>

